@import url(https://fonts.googleapis.com/css?family=Lora:400,700);
@import url(https://fonts.googleapis.com/css?family=Princess+Sofia|Roboto+Condensed);
@import url(https://cdnjs.cloudflare.com/ajax/libs/bulma/0.6.1/css/bulma.min.css);

html,
body {
  background-color: #F1F0EC;
  height: 100%;
  font-family: 'Roboto Condensed', sans-serif;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #152624;
  padding: 20px;
}
@-webkit-keyframes animate-width {
  0% {
    width: 0;
  }
  100% {
    visibility: visible;
  }
}
@keyframes animate-width {
  0% {
    width: 0;
  }
  100% {
    visibility: visible;
  }
}
.progress-bar {
  background-color: #152624;
  -webkit-box-shadow: inset 0 5px 15px rgba(0, 0, 0, 0.6);
          box-shadow: inset 0 5px 15px rgba(0, 0, 0, 0.6);
  margin: 0 0 10px;
}

.progress-bar-rating {
  position: relative;
  border-radius: 4px;
  display: inline-block;
  width: 0;
  height: 10px;
  font-size: 10px;
  text-indent: -999em;
  overflow: hidden;
  line-height: 10px;
  color: white;
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  -webkit-box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.15);
          box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.15);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#F15471), color-stop(47%, #F15471), color-stop(50%, #ED143D), to(#ED143D));
  background-image: -webkit-linear-gradient(top, #F15471 0%, #F15471 47%, #ED143D 50%, #ED143D 100%);
  background-image: -o-linear-gradient(top, #F15471 0%, #F15471 47%, #ED143D 50%, #ED143D 100%);
  background-image: linear-gradient(to bottom, #F15471 0%, #F15471 47%, #ED143D 50%, #ED143D 100%);
  width: 0;
  -webkit-transition: all 0.25s cubic-bezier(0.26, 0.01, 0.51, 1);
  -o-transition: all 0.25s cubic-bezier(0.26, 0.01, 0.51, 1);
  transition: all 0.25s cubic-bezier(0.26, 0.01, 0.51, 1);
}

.button{
  background:transparent;
  -webkit-transition:all .5s ease;
  -o-transition:all .5s ease;
  transition:all .5s ease;
  color:#152624;
  outline:none;
  -webkit-box-shadow: 20px 38px 34px -26px hsla(0,0%,0%,.2);
          box-shadow: 20px 38px 34px -26px hsla(0,0%,0%,.2);
  border-radius: 255px 15px 225px 15px/15px 225px 15px 255px;
}
.button:hover{
  -webkit-box-shadow:2px 8px 4px -6px hsla(0,0%,0%,.3);
          box-shadow:2px 8px 4px -6px hsla(0,0%,0%,.3);
}

.button.lined.thin{
  border:solid 2px #152624;
}


.App-title {
  font-size: 1.5em;
  font-family: 'Princess Sofia', cursive;
  color: #ED143D;
  text-shadow: 0 1px 0 rgba(255, 255, 255, .09);
}

.App-intro {
  font-size: large;
  margin-bottom: 10px;
}

.App-room {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  padding: 25px 0;
  overflow: hidden;
}
@media (max-width: 576px) {
  .App-room > .card:not(:first-child) { margin: 0 0 0 -75px; }
}

.App-shield {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: -75px;
}
@media (max-width: 576px) {
  .App-shield { margin-top: -150px; }
}
.App-shield .card {
  -webkit-box-shadow: 0 0 0 10px rgba(67, 170, 139, .1), 0 10px 30px rgba(67, 170, 139, 0.5), inset 0 0 0 1px rgba(0, 0, 0, 0.2);
          box-shadow: 0 0 0 10px rgba(67, 170, 139, .1), 0 10px 30px rgba(67, 170, 139, 0.5), inset 0 0 0 1px rgba(0, 0, 0, 0.2);
  width: 180px;
  height: 230px;
}



